  background-color: rgba(200, 206, 211, 0.4); }

.PaymentMethods img {
  width: 80px; }

.PaymentMethods .cardheader {
  vertical-align: middle; }


.Users .table td {
  vertical-align: middle; }

.Users .table > tbody > tr:nth-child(2n+1) > td, .Users .table > tbody > tr:nth-child(2n+1) > th {
  background-color: rgba(200, 206, 211, 0.4); }

.Users img {
  width: 80px; }

.Users .cardheader {
  vertical-align: middle; }

.AddStoreModal .error {
  display: unset;
  width: 100%;
  margin-top: 0.25rem;
  font-size: 80%;
  color: #f86c6b; }

.AddStoreModal .error-select .Select-control {
  border: 1px solid red; }




